.game-main {
  display: flex;
}

.game-image-pokemon {
  width: 50%;
  height: 100%;

  display: flex;
  justify-content: center;
  align-items: center;

  header {
    position: absolute;
    top: 24px;
    left: 0;

    padding: 0 160px;
  }
}

.game-name-pokemon {
  background-color: var(--yellow);
  width: 50%;

  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;

  padding: 4rem 0;
}
